<template>
    <v-container style="width: 100%; " align="center" fluid>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col></v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{usersText}}</v-toolbar-title>
                </v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0">
                    <v-btn class="app-bar-button white--text" small tile style="display: inline;" text
                           @click="openSearchFilter">{{toFilterText}}
                    </v-btn>
                </v-col>
            </v-row>


        </v-app-bar>

        <div style="margin-top: 64px"/>

        <v-row class="text-center" style="height: 58px; padding-bottom: 0px">
            <v-col style="padding-right: 0px; padding-top: 4px">
                <v-text-field
                        v-model="searchByUsernameField"
                        :rules="[rules.min5Char]"
                        :label="searchLabel"
                        solo
                        dense
                        clearable
                        rounded
                ></v-text-field>
            </v-col>
            <v-col cols="auto" style="padding-left: 0px; padding-top: 4px; padding-right: 12px">
                <v-btn class="mx-2" fab dark small color="primary" style="margin-right: 0px!important"
                       @click="listUsersByUsername">
                    <v-icon dark>mdi-magnify</v-icon>
                </v-btn>
            </v-col>
        </v-row>

        <v-row class="text-center" v-if="filtersOn">
            <v-col class="pa-0" style="margin-bottom: 8px">
                <v-btn rounded color="primary" dark small @click="resetSearchingFilters">
                    <v-icon small left>mdi-close-circle</v-icon>
                    {{removeFiltersText}}
                </v-btn>
            </v-col>
        </v-row>

        <v-row v-if="users.length > 0" class="text-center justify-center" style="padding: 8px 2% 80px 2%">
            <div v-for="user in users"
                 :key="user.id"
                 style="margin-bottom: 68px!important">
                <v-card
                        class="text-center ma-1"
                        style="padding: 0px; width: available; min-width: 105px; max-width: 150px; display: inline-block; margin: auto;">
                    <v-card
                            elevation="0" @click="openUserProfile(user.id)" style="padding: 4px;">
                        <v-img
                                class="white--text align-end"
                                :aspect-ratio="1"
                                :src="user.profileImg"
                        ></v-img>
                        <div>
                            <strong class="secondary--text" style="font-size: 11px">@{{user.username}}</strong>
                        </div>
                    </v-card>
                    <v-card
                            style="padding: 0px; width: 100%; position: absolute; left: 0px; top: calc(100% + 6px);"
                    >
                        <div
                                class="scrolling-wrapper-flexbox"
                                style="width: calc(100% - 8px); margin-left: 4px"
                        >
                            <div
                                    v-for="lan in getLanguagesByUserID(user.id)"
                                    :key="lan" class="flag-card ma-1">
                                <v-avatar
                                        style="height: 16px !important; width: 16px !important; min-width: 16px!important;">
                                    <img :src="createPathToFlagImage(lan)" :aspect-ratio="1"
                                         style=" object-position: center; object-fit: cover;">
                                </v-avatar>
                            </div>
                        </div>
                        <!--<div class="text-center justify-center" style="height: 32px; display: flex; width: calc(100% - 8px); padding-left: 6px!important; vertical-align: middle;">
                            <p style="font-size: 16px; color: gray; vertical-align: bottom;display: block; margin: 0px;"><</p>
                            <div
                                    class="scrolling-wrapper-flexbox"
                                    style=" width: calc(100% - 16px)"
                            >
                                <div
                                        v-for="lan in getLanguagesByUserID(user.id)"
                                        :key="lan" class="flag-card ma-1">
                                    <v-avatar
                                            style="height: 16px !important; width: 16px !important; min-width: 16px!important;">
                                        <img :src="createPathToFlagImage(lan)" :aspect-ratio="1"
                                             style=" object-position: center; object-fit: cover;">
                                    </v-avatar>
                                </div>
                            </div>
                            <p style="font-size: 16px; color: gray;">></p>
                        </div>-->
                    </v-card>
                </v-card>
            </div>
            <v-col v-show="users.length == 0">
                <p>{{messageNoUsersFit}}</p>
            </v-col>

        </v-row>
        <v-btn
                v-scroll="onScroll"
                style="z-index: 3; bottom: 80px"
                v-show="fabScroll && users.length > 0"
                fab
                dark
                fixed
                right
                color="primary"
                @click="toTop"
        >
            <v-icon>mdi-arrow-up</v-icon>
        </v-btn>
        <v-row v-if="pageLoading" class="text-center justify-center">
            <v-progress-circular
                    indeterminate
                    color="primary"
                    style="margin-bottom: 50px"
            ></v-progress-circular>
        </v-row>
        <!--<v-row v-if="users.length > 0" class="text-center ma-2">
            <v-col>
                <v-btn v-show="!buttonIsDisabled" color="primary" @click="listUsers" :disabled="buttonIsDisabled">
                    {{watchMoreButtonText}}
                </v-btn>
            </v-col>
        </v-row>-->

        <v-card v-if="users.length == 0 && (filtersOn || usernameFilter != null) && usersListed" class="pa-2 ma-1"
                style="padding-bottom: 24px!important; height: 66vh">
            <v-row class="text-center justify-center">
                <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noUsersText}}</p>
            </v-row>
            <v-row class="text-center justify-center">
                <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
            </v-row>
        </v-card>


        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions, mapState, mapGetters, mapMutations} from 'vuex';

    export default {
        name: "SearchHome",
        data() {
            return {
                buttonIsDisabled: false,
                snackbar: false,
                snackbarText: null,
                searchLabel: null,
                noUsersText: null,
                usersText: null,
                toFilterText: null,
                removeFiltersText: null,
                closeText: null,
                searchByUsernameField: null,
                searchButtonDisabled: true,
                messageNoUsersFit: '',
                rules: {
                    min5Char: value => (value == null || value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME || value.length == 0) || this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_USERNAME_TO_START_SEARCHING'),
                },
                filtersOn: false,
                sadFaceImg: Constants.IMG_EMOJI_SAD_FACE,
                usersListed: false,
                pageLoading: false,
                fabScroll: false,
                ruleTextMinChar: null
            }
        },
        methods: {
            ...mapActions('users', ['getSearchedUsers', 'resetSearchedUsers', 'resetFilters']),
            ...mapMutations('users', ['setSearchByUsernameFilter']),
            onScroll(e) {
                if (typeof window === 'undefined') return
                const top = window.pageYOffset || e.target.scrollTop || 0
                this.fabScroll = top > 20
            },
            toTop() {
                this.$vuetify.goTo(0)
            },
            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },
            getLanguagesByUserID(userID) {
                return this.$store.getters['users/getLanguagesByUserID'](userID);
            },
            listUsers: function () {
                let self = this;
                self.buttonIsDisabled = true;
                self.usersListed = false;

                let resultGetSearchedUsers = self.getSearchedUsers();

                resultGetSearchedUsers.then((moreUsersToShow) => {
                    self.usersListed = true;
                    self.buttonIsDisabled = !moreUsersToShow;
                    self.pageLoading = false;
                })
                    .catch((error) => {
                        self.pageLoading = false;
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            listUsersByUsername: async function () {
                let self = this;

                if (self.searchByUsernameField && self.searchByUsernameField.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME) {
                    self.searchButtonDisabled = true;
                    let $ = require('jquery');
                    self.usersListed = false;

                    await self.resetSearchedUsers().then(() => {
                        self.filtersOn = self.$store.getters['users/areThereFiltersOn'];
                    }).catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

                    console.log("-->" + self.searchByUsernameField.toLowerCase())
                    self.setSearchByUsernameFilter(self.searchByUsernameField.toLowerCase());

                    let first = true;
                    let available = true;
                    self.buttonIsDisabled = true;

                    while (first || (available && $(document).height() <= $(window).height())) {
                        first = false;

                        await self.getSearchedUsers().then((moreUsersToShow) => {
                            available = moreUsersToShow;
                            self.pageLoading = false;
                        })
                            .catch((error) => {
                                self.pageLoading = false;
                                self.snackbarText = error;
                                self.snackbar = true;
                            });
                    }
                    self.buttonIsDisabled = !available;
                    self.usersListed = true;
                }

            },
            resetSearchingFilters: async function () {
                let self = this;

                self.usersListed = false;

                await self.resetFilters().catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });

                this.searchByUsernameField = null;

                await self.initUsers();

            },
            openUserProfile(id) {
                this.$router.push('search/user/' + id);
            },
            openSearchFilter() {
                this.searchByUsernameField = null;
                this.setSearchByUsernameFilter(this.searchByUsernameField);
                this.$router.push('search/filter');
            },
            initUsers: async function () {
                let self = this;
                let $ = require('jquery');

                self.usersListed = false;


                await self.resetSearchedUsers().catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });

                let first = true;
                let available = true;
                self.buttonIsDisabled = true;

                while (first || (available && $(document).height() <= $(window).height())) {
                    first = false;

                    await self.getSearchedUsers().then((moreUsersToShow) => {
                        available = moreUsersToShow;
                        self.filtersOn = self.$store.getters['users/areThereFiltersOn'];
                        self.pageLoading = false;
                    })
                        .catch((error) => {
                            self.pageLoading = false;
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
                self.buttonIsDisabled = !available;
                self.usersListed = true;
            }
        },
        created: function () {
            this.searchLabel = this.$i18n.t('messages.TEXT_SEARCH');
            this.noUsersText = this.$i18n.t('messages.TEXT_NO_USERS_MATCH_SEARCH');
            this.usersText = this.$i18n.t('messages.TEXT_USERS');
            this.toFilterText = this.$i18n.t('messages.TEXT_TO_FILTER');
            this.removeFiltersText = this.$i18n.t('messages.TEXT_REMOVE_FILTERS');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');

            this.initUsers();
        },
        mounted() {
            let self = this;
            let $ = require('jquery');

            $(window).scroll(function () {
                let bottom_position = $(document).height() - ($(window).scrollTop() + $(window).height());
                //console.log(bottom_position)

                if (!self.buttonIsDisabled && bottom_position < 1) {
                    self.pageLoading = true;
                    self.listUsers();
                }
            });
        },
        computed: {
            ...mapGetters('users', {users: 'getSearchedUsers'}),
            ...mapGetters('users', {usernameFilter: 'getSearchByUsernameFilter'}),
        },
        watch: {

            searchByUsernameField: async function (value) {
                if (value == '') {
                    this.searchByUsernameField = null;
                }

                if (value == null && value != this.$store.getters['users/getSearchByUsernameFilter']) {
                    await this.resetFilters().catch((error) => {
                        self.setSearchByUsernameFilter(null);
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

                    await this.initUsers();
                }
            }
        }
        ,
    }
</script>

<style scoped>
    .scrolling-wrapper-flexbox {
        display: flex;
        flex-wrap: nowrap;
        overflow-x: scroll;
        scrollbar-arrow-color: darkblue;
        padding-bottom: 2px;
    }

    .flag-card {
        flex: 0 0 auto;
    }

    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }

    .filter-cards-titles {
        padding: 10px;
        padding-bottom: 5px;
        font-size: 18px;
    }

    .v-input__slot {
        margin: 0px;
    }

    p {
        word-wrap: inherit;
    }
</style>
